


































































































	import server from '../../../store/server.js';
	export default {
		data() {
			return {
				Token: '',
				areaVal: 0,
				areaList: [{
					value: 1,
					text: '测区1'
				}],
				ProjectName: '',
				TestState: '',
				ComponentType: '',
				Grade: '',
				CheckDate: '',
				ConcreteDate:'',
				dataNumList: [],
				Calculation: '',
				Remarks: '',
				Floor:''
			}
		},
		onLoad(option) {
			this.Token = server.net.GetSessionToken();
			this.getDetails(option.id);
		},
		methods: {
			getDetails(id) {
				let url = server.net.setUrl('api/QualityConcreteBackApi/GetDetail');
				let ajaxdata = JSON.stringify({
					ID: id
				})
				uni.request({
					url: url,
					method: 'POST',
					dataType: 'json',
					header: {
						Token: this.Token,
						"content-type": "application/json"
					},
					data: ajaxdata,
					success: res => {
						server.net.showWrongTipsEx(res);
						let result = res.data;
						let jsondata = result.data;

						if (result.status == 1) {
							//遍历JSON数组，获取数据对象
							jsondata.forEach((e) => {
								this.ProjectName = e.ProjectName;
								this.TestState = e.TestState;
								this.CheckDate = e.CheckDate.substring(0, 10);
								this.Grade = e.Grade;
								this.ComponentType = e.ComponentType;
								this.Calculation = e.Calculation;
								this.Remarks = e.Remark;
								this.Floor = e.Floor;
								this.ConcreteDate = e.ConcreteDate.substring(0, 10);
								let arr = e.DetailCollection;
								for (let i in arr) {
                                    this.dataNumList.push(
									{
										"index":arr[i].Name,
										"value":arr[i].DataAttr.join(" ")
									})
								}
								console.log(e);
							});
						} else {
							console.log(result.message);
							uni.showToast({
								title: result.message
							})
						}

					},
					fail: (code) => {
						console.log(code)
						uni.showToast({
							title: '数据获取失败，请检查您的网络环境'
						});
					},

				});
			}
		}
	}



